import React, { useCallback } from "react";
import { useNavigate } from "react-router";
import { routeNames } from "../routes/routeName";

export const Header = () => {
  const navigate = useNavigate();

  const onItemClick = useCallback((link) => {
    if (link) {
      navigate(link);
    }
  }, []);

  return (
    <>
      {/* Header */}
      <header
        id="header"
        className="header d-flex align-items-center light-background sticky-top"
      >
        <div className="container-fluid position-relative d-flex align-items-center justify-content-between">
          <a
            href="index.html"
            className="logo d-flex align-items-center me-auto me-xl-0"
          >
            {/* <img src="assets/img/logo.png" alt="" /> */}
            <h1 className="sitename">Tulsiram</h1>
          </a>

          <nav id="navmenu" className="navmenu">
            <ul>
              <li>
                <a onClick={() => onItemClick(routeNames.home)}>Home</a>
              </li>
              <li>
                <a onClick={() => onItemClick(routeNames.about)}>About</a>
              </li>
              <li>
                <a onClick={() => onItemClick(routeNames.resume)}>Resume</a>
              </li>
              {/* <li>
                <a onClick={() => onItemClick(routeNames.portfolio)}>Portfolio</a>
              </li> */}
              <li>
                <a onClick={() => onItemClick(routeNames.contact)}>Contact</a>
              </li>
            </ul>
            <i className="mobile-nav-toggle d-xl-none bi bi-list"></i>
          </nav>

          <div className="header-social-links">
            <a href="#" className="linkedin">
              <i className="bi bi-linkedin"></i>
            </a>
          </div>
        </div>
      </header>
    </>
  );
};
