import { createHashRouter as createBrowserRouter } from "react-router-dom";
import * as Page from "../pages";
import { routeNames } from "./routeName";

export const router = createBrowserRouter([
  {
    path: "/",
    children: [
      {
        path: "/",
        element: <Page.Home />,
      },
      {
        path: routeNames.about,
        element: <Page.About />,
      },
      {
        path: routeNames.resume,
        element: <Page.Resume />,
      },
      {
        path: routeNames.portfolio,
        element: <Page.Portfolio />,
      },
      {
        path: routeNames.portfolio_details,
        element: <Page.PortfolioDetail />,
      },
      {
        path: routeNames.contact,
        element: <Page.Contact />,
      },
    ],
  },
]);
