import React, { useCallback, useEffect } from "react";
import { Header } from "../components/Header";
import { Footer } from "../components/Footer";
import AOS from "aos";
import Swiper from "swiper";
import GLightbox from "glightbox";
import { projectList } from "../constant/project_data";
import { useNavigate } from "react-router";
import { routeNames } from "../routes/routeName";

const Portfolio = () => {
  const navigate = useNavigate();
  useEffect(() => {
    AOS.init({
      duration: 1000,
      once: true,
    });

    GLightbox();

    new Swiper(".swiper", {
      // Example Swiper options
      slidesPerView: 1,
      spaceBetween: 10,
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
    });
  }, []);


  const onViewProject = useCallback((item,index)=>{
    navigate(routeNames.portfolio_details,{item,index})
  },[])

  return (
    <>
      <Header />

      <main class="main">
        <section id="portfolio" class="portfolio section">
          <div class="container section-title" data-aos="fade-up">
            <h2>Portfolio</h2>
            <p>
              Necessitatibus eius consequatur ex aliquid fuga eum quidem sint
              consectetur velit
            </p>
          </div>

          <div class="container">
            <div
              class="isotope-layout"
              data-default-filter="*"
              data-layout="masonry"
              data-sort="original-order"
            >
              {/* <ul
                class="portfolio-filters isotope-filters"
                data-aos="fade-up"
                data-aos-delay="100"
              >
                <li data-filter="*" class="filter-active">
                  All
                </li>
                <li data-filter=".filter-app">App</li>
                <li data-filter=".filter-product">Card</li>
                <li data-filter=".filter-branding">Web</li>
              </ul> */}

              <div
                class="row gy-4 isotope-container"
                data-aos="fade-up"
                data-aos-delay="200"
              >
                {projectList.map((item, index) => {
                  return (
                    <div
                      
                      key={String(index)}
                      class="col-lg-4 col-md-6 portfolio-item isotope-item filter-app"
                    >
                      <img
                        src={item?.image}
                        class="img-fluid"
                        alt=""
                      />
                      <div class="portfolio-info">
                        <h4>{item?.title}</h4>
                        <p>{item?.des}</p>
                        <a
                          href={item?.href}
                          title={item?.title}
                          data-gallery="portfolio-gallery-app"
                          class="glightbox preview-link"
                        >
                          <i class="bi bi-zoom-in"></i>
                        </a>
                        <a
                          onClick={()=>onViewProject(item,index)}
                          title="More Details"
                          class="details-link"
                        >
                          <i class="bi bi-link-45deg"></i>
                        </a>
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        </section>
      </main>

      <Footer />
    </>
  );
};

export default Portfolio;
